import React, { Component } from 'react';
import {
    Button,
    List,
    Grid,
    Segment,
    TransitionablePortal
} from 'semantic-ui-react';
import color from '../colors'

class Keypad extends Component {
    state = {keepKepadOpen :false};

    keepKeypadOpen = () => {
        this.setState({keepKeypadOpen:!this.state.keepKeypadOpen})
      }
        
  render() {
    return (
      <div>
        <TransitionablePortal open={this.props.open} transition={{animation:'fade up', duration:300}} closeOnDocumentClick={!this.state.keepKeypadOpen} onClose={this.props.onClose}>
          <Segment textAlign="center" inverted style={{ backgroundColor: '#3A3A3A', left: '0%', padding:0, position: 'fixed', bottom: '0px', zIndex: 5000, width:"100%", height:"auto", borderRadius:0}}>
            <Segment style={{borderRadius:0, padding:0}} >
              <Grid widths={16} style={{padding:14}} >
                <Grid.Column 
                    width={11} 
                    style={{borderRadius:0, borderStyle:"none",padding:14, color:color(this.props.item.tags), backgroundColor:"#DFE0E1"}}
                    onClick={this.props.itemClicked}
                >
                  <strong>{this.props.item.name + (this.props.item.marked ? " *" : "")}</strong>
                </Grid.Column>
                <Grid.Column verticalAlign='middle' width={5} style={{fontSize:17, fontWeight:'bold' }}>
                  {this.props.item.qty + " " + this.props.item.unit}
                </Grid.Column>
              </Grid>
            </Segment>
            <List selection divided inverted horizontal style={{marginTop:-18, marginBottom:-16}}>
              <List.Item style={{padding:12}}/>
              <List.Item style={{padding:12}} onClick={this.props.unitClicked}>bags</List.Item>
              <List.Item style={{padding:12}} onClick={this.props.unitClicked}>boxes</List.Item>
              <List.Item style={{padding:12}} onClick={this.props.unitClicked}>trays</List.Item>
              <List.Item style={{padding:12}} onClick={this.props.unitClicked}>tubs</List.Item>
              <List.Item style={{padding:12}} onClick={this.props.unitClicked}>bin</List.Item>
              <List.Item style={{padding:12}} onClick={this.props.unitClicked}>shelf</List.Item>
              <List.Item style={{padding:12}}/>
            </List>
            <Segment inverted basic className="ui black" style={{borderRadius:0}}>
              <Button.Group size="big" widths="4" >
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>1</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>2</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>3</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}></Button>
              </Button.Group>
              <Button.Group size="big" widths="4" >
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>4</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>5</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>6</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}></Button>
              </Button.Group>
              <Button.Group size="big" widths="4" >
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>7</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>8</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>9</Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}></Button>
              </Button.Group>
              <Button.Group size="big" widths="4" >
                <Button className="ui black button" icon="asterisk" onClick={this.props.asteriskClicked} style={{margin:0, borderRadius:"0em"}}></Button>
                <Button className="ui black button" onClick={this.props.numberClicked} style={{margin:0, borderRadius:"0em"}}>0</Button>
                <Button className="ui black button" icon="delete" onClick={this.props.deleteClicked} style={{margin:0, borderRadius:"0em"}}></Button>
                {/* <Button className="ui black button" icon="angle down" onClick={this.closeKeypad} style={{margin:0, borderRadius:"0em"}}></Button> */}
                <Button className="ui black button" icon={this.state.keepKeypadOpen?"lock":"unlock"} onClick={this.keepKeypadOpen} style={{margin:0, borderRadius:"0em"}}></Button>
              </Button.Group>
            </Segment>
          </Segment>
        </TransitionablePortal>
      </div>
    );
  }
}

export default Keypad;



